@charset "UTF-8";
/*  tamaños para media queries (desde este tamaño en adelante, ej: @media (min-width: $tablet) */
/* fuente principal */
/* colores */
/* Reseteamos los margin y paddings de todas las etiquetas */
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

*::before, *::after {
  display: block;
}

/* Evitamos problemas con las imagenes */
img, picture, video, iframe, figure {
  display: block;
  max-width: 100%;
  width: 100%;
  /* (Opcional) */
  object-fit: cover;
  /* (Opcional) */
  object-position: center center;
}

/* Reseteamos los enlaces para funcionar como cajas... */
a {
  display: block;
  cursor: pointer;
}

/* ... excepto los que se encuentran en párrafos */
p a {
  display: inline;
}

/* Quitamos los puntos de los <li> */
li {
  list-style-type: none;
}

/* (Opcional) Configuramos anclas suaves */
html {
  scroll-behavior: smooth;
}

/* Desactivamos estilos por defecto de las principales etiquetas de texto */
h1, h2, h3, h4, h5, h6, p, span, a, strong, blockquote, i, b, u, em {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  text-transform: none;
  color: inherit;
}

/* Evitamos problemas con los pseudoelementos de quotes */
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

/* (Opcional) Configuramos el texto que seleccionamos */
::selection {
  background-color: #000;
  color: #fff;
}

/* Nivelamos problemas de tipografías y colocación de formularios */
form, input, textarea, select, button, label {
  font-family: inherit;
  font-size: inherit;
  hyphens: auto;
  background-color: transparent;
  color: inherit;
  display: block;
  /* (Opcional) */
  appearance: none;
}

/* Reseteamos las tablas */
table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Evitamos problemas con los SVG */
svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

/* Configuramos la tipografía para toda la web */
body {
  min-height: 100vh;
  font-size: 100%;
  /* /Opcional) */
  line-height: 1.4em;
  /* (Opcional) */
  -webkit-font-smoothing: antialiased;
  /* (Opcional) */
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4 {
  font-weight: 600;
  text-align: center;
  font-family: Verdana;
}

h4 {
  font-weight: normal;
}

body {
  max-width: 112.5rem;
  margin: 0 auto;
  font-family: Helvetica Arial sans-serif;
  color: #000;
  background-color: #fff;
}

.o-container {
  display: block;
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.c-header__contenedor {
  margin: 0 auto;
  padding: 0.5rem;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 960px) {
  .c-header__contenedor {
    padding: 1rem;
    width: 80%;
  }
}
.c-header__cabecera {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .c-header__cabecera {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
  }
}
.c-header__cabecera-izq {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.c-header__logo {
  max-width: 15.6rem;
}
@media (min-width: 960px) {
  .c-header__logo {
    max-width: 31.25rem;
    margin-left: 1rem;
  }
}
@media (min-width: 960px) {
  .c-header__sociales {
    padding-right: 1rem;
  }
}
.c-header__nav {
  width: 100%;
}

.c-nav__ul {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .c-nav__ul {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
  }
}
.c-nav__li {
  padding: 1rem;
}
.c-nav__li {
  font-size: 1.5em;
}

.c-publicidad1 {
  display: none;
}
@media (min-width: 960px) {
  .c-publicidad1 {
    display: block;
  }
}
.c-publicidad1__contenedor {
  margin: 0 auto;
  height: 15rem;
  width: 80%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  border: 1px solid #000;
}
.c-publicidad1__contenedor::before {
  content: "publicidad 1";
  text-transform: uppercase;
  font-size: 1.5em;
  font-weight: bold;
}

.c-sociales {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.c-sociales__li {
  padding: 0.5rem;
  font-size: 1em;
}
@media (min-width: 768px) {
  .c-sociales__li {
    padding: 0.8rem;
    font-size: 1.3em;
  }
}
.c-sociales__li:first-child {
  color: #1877f2;
}
.c-sociales__li:nth-child(2) {
  color: #1da1f2;
}
.c-sociales__li:nth-child(3) {
  color: #ff0000;
}
.c-sociales__li:nth-child(4) {
  color: #e60023;
}
.c-sociales__li:nth-child(5) {
  color: #405de6;
}
.c-sociales__li:nth-child(6) {
  color: #000;
}
.c-sociales__li:nth-child(7) {
  color: #5865f2;
}

.u-flex {
  display: flex !important;
}

.u-rotation {
  transform: rotate(180deg);
}